.navbar {
  &__nav{
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  &__link{
    display: flex;
    flex-direction: row;
    align-items: center;
    column-gap: 20px;
    flex-wrap: wrap;
    &--squeeze{
      justify-content: center;
    }
  }
  &__symbol{
    font: var(--font-m);
    flex: 1 1 auto;
    text-align: center;
  }
  &__ico{
    max-width: 30px;
  }
}